footer {
    background-color: #f2d3d3;
    display: flex;
    justify-content:space-evenly;
    height:400px;
  }
  .link-col {
    margin-top:100px;
    display: flex;
    flex-direction: column;
    gap:5%;
  }
  h3{
    font-family: "Poppins", sans-serif;
  }
  .brandname-last {
    font-family: "Poppins", sans-serif;
    font-size: 2.2rem;
    font-weight: 1000;
    margin-left: 7rem;
    line-height:400px;
  }
  .footer-products{
    font-family:"Poppins", sans-serif;
  }
  .footer-products:hover{
    color: #2692dd;
  }
  .sub-footer{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .subfooter-text{
    font-family:"Poppins", sans-serif;
    line-height:100px;
  }