.services {
    width: 100%;
    height: 300px;
    padding-top: 50px;
    padding-bottom: 50px;
  }
  .section-card {
    padding-top: 50px;
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    gap:5%;

  }
  .section-title {
    line-height: 25px;
    font-size: 1.5rem;
    font-weight: 1000px;
    font-style: bold;
    text-align: center;
    font-family: "Poppins", sans-serif;
    width: 100%;
    height: 25px;
  }
  .card {
    text-align: left;
    object-fit: cover;
    width: 20%;
    height: 275px;
    border-radius: 8px;
  }
  .card img {
    object-fit: cover;
    width: 150px;
  }
  #img1,
  #img2,
  #img3,
  #img4 {
    border-radius: 5px 5px 5px 5px;
    width: 100%;
    height: 200px;
    margin-bottom: 10px;
  }
  .card h4,
  .card p {
    padding-left: 10px;
  }
  .card h4:hover {
    cursor: pointer;
    color: rgb(8, 151, 212);
  }