.doctor-info{
    width:100%;
    padding-bottom:50px;
    background-color: #abecdf;
  }
  .info-header{
    width:100%;
    text-align: center;
    padding-top:50px;
    height:50px;
  }
  .info-heading{
    font-family: "Poppins", sans-serif;
    line-height:50px;
    font-size:2rem;
    font-weight:700;
  }
  .info-area{
    margin-top:50px;
    display: flex;
    justify-content: center;
    gap:1%;
    margin-left: 2%;
    margin-right: 2%;
  }
  .info-card{
    font-family: "Poppins", sans-serif;
    background-color: #55AD9B;
    width:15%;
    height: 40%;
    padding:10px;
    padding-bottom:40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius:5px;
  }
  #dr1,#dr2,#dr3,#dr4,#dr5,#dr6{
    height:100px;
    width:100px;
    border-radius:50%;
    object-fit: cover;
  }
  .dr-heading{
    margin-top:10px;
    margin-bottom:10px;
    font-size:1.5rem;
  }
  .dr-button{
    background-color:white;
    
    font-size:1.2rem;
    padding:20px;
    font-family: "Poppins", sans-serif;
    border-radius: 10px;
  }
  .last-dr-button{
    background-color:white;
    border:1px solid black;
    font-size:1.5rem;
    padding:20px;
    font-family: "Poppins", sans-serif;
    border-radius: 10px;
  }
  .dr-button:hover{
    background-color:#D8EFD3; 
    font-weight:700;
  }
  .info-card .dr-info{
    margin: 0;
    padding-bottom:20px;
    text-align: center;
  }