.navbar {
    background-color: antiquewhite;
    display: flex;
    height: 70px;
    position: fixed;
    width:100%;
  }
  .brandname {
    font-family: "Poppins", sans-serif;
    font-size: 2.2rem;
    font-weight: 1000;
    line-height: 70px;
    margin-left: 7rem;
  }
  .components {
    display: flex;
    width: 75%;
  }
  a {
    text-decoration: none;
    color: black;
  }
  .home,
  .consult,
  .about {
    font-family: "Poppins", sans-serif;
    font-weight: 500;
    font-size: 1.1rem;
    line-height: 72px;
    margin-left: 2.5rem;
  }
  .home:hover,
  .consult:hover,
  .about:hover {
    font-weight: 550;
    font-size: 1.2rem;
    cursor: pointer;
  }
  .login {
    font-family: "Poppins", sans-serif;
    font-weight: 500;
    font-size: 0.8rem;
    margin-left: auto;
    margin-top: 15px;
    height: 40px;
    width: 7.5rem;
    background-color: cadetblue;
    border: none;
    border-radius: 10px;
    line-height: 40px;
    text-align: center;
  }
  .login:hover {
    background-color: rgb(59, 117, 119);
  }