.search-tab{
  background-color: #2691dd4f;
    margin-top:50px;
    width:100%;
    height:200px;
    font-family: "Poppins", sans-serif;
    text-align: center;
    font-size:xx-large;
}
h5{
  padding-top:30px;
}
.search{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top:20px;
  }
  .searchform {
    background: #f4f4f4;
    background: rgba(244, 244, 244, 0.79);
    border: 1px solid #d3d3d3;
    padding: 2px 5px;
    width:70%;
    height:40px;
    box-shadow: 0 4px 9px rgba(0, 0, 0, 0.37);
    -moz-box-shadow: 0 4px 9px rgba(0, 0, 0, 0.37);
    -webkit-box-shadow: 0 4px 9px rgba(0, 0, 0, 0.37);
    border-radius: 10px;
    -moz-border-radius: 10px;
    -webkit-border-radius: 10px;
  }
  .searchform input{
    float: left;
  }
  .searchform button {
    float: right;
  }
  .searchform input {
    background: #fefefe;
    border: none;
    font:20px/20px ;
    font-family: "Poppins", sans-serif;
    font-weight: bolder;
    padding: 10px;
    width:40%;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.4) inset,
      1px 1px 1px rgba(255, 255, 255, 0.75);
    -moz-box-shadow: 0 0 4px rgba(0, 0, 0, 0.4) inset,
      1px 1px 1px rgba(255, 255, 255, 0.75);
    -webkit-box-shadow: 0 0 4px rgba(0, 0, 0, 0.4) inset,
      1px 1px 1px rgba(255, 255, 255, 0.75);
    border-radius: 9px;
    -moz-border-radius: 9px;
    -webkit-border-radius: 9px;
  }
  .searchform input:focus {
    outline: none;
    box-shadow: 0 0 4px #0d76be inset;
    -moz-box-shadow: 0 0 4px #0d76be inset;
    -webkit-box-shadow: 0 0 4px #0d76be inset;
  }
  .searchform input::-webkit-input-placeholder {
    font-style: italic;
    line-height: 15px;
  }
  .searchform input:-moz-placeholder {
    line-height: 15px;
  }
  .searchform button {
    background: rgb(52, 173, 236);
    background: -moz-linear-gradient(
      top,
      rgba(52, 173, 236, 1) 0%,
      rgba(38, 145, 220, 1) 100%
    );
    background: -webkit-linear-gradient(
      top,
      rgba(52, 173, 236, 1) 0%,
      rgba(38, 145, 220, 1) 100%
    );
    background: -o-linear-gradient(
      top,
      rgba(52, 173, 236, 1) 0%,
      rgba(38, 145, 220, 1) 100%
    );
    background: -ms-linear-gradient(
      top,
      rgba(52, 173, 236, 1) 0%,
      rgba(38, 145, 220, 1) 100%
    );
    background: linear-gradient(
      to bottom,
      rgba(52, 173, 236, 1) 0%,
      rgba(38, 145, 220, 1) 100%
    );
    border: none;
    color: #fff;
    cursor: pointer;
    font-size:20px;
    font-family: "Poppins", sans-serif;
    height:100%;
    width: 16.2%;
    margin-right:2px;
    box-shadow: 0 0 2px #2692dd inset;
    -moz-box-shadow: 0 0 2px #2692dd inset;
    -webkit-box-shadow: 0 0 2px #2692dd inset;
    border-radius: 9px;
    -moz-border-radius: 9px;
    -webkit-border-radius: 9px;
  }
  .searchform button:hover {
    opacity: 0.9;
  }
  i{
    font-size: 20px;
    margin-right:5px;
  }